.page-header {
  background-color: #1b2d37;
}

.page-header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  max-width: 1420px;
  margin: 0 auto;
  padding: 32px 10px 23px;
  z-index: 5;

  @media (max-width: 1023px) {
    max-width: 100%;
    padding: 21px 24px 0;
  }

  @media (max-width: 767px) {
    padding: 17px 16px 0;
  }
}

.page-header__logo {
  padding-left: 3px;
  margin-right: 100px;
}

.page-header__logo img {
  @media (max-width: 767px) {
    width: 38px;
    height: 38px;
  }
}

.page-header__button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 180px;
  margin: auto 0;
  margin-right: 10px;
  padding: 14px 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  background-color: transparent;
  border: 1px solid $white;
  border-radius: 5px;
  cursor: pointer;
  box-sizing: border-box;

  &:hover {
    background-color: $blue;
  }

  &:active {
    opacity: 0.5;
  }

  &:focus {
    outline: 2px solid orange;
  }

  @media (min-width: 1024px) and (max-width: 1420px) {
    margin-right: 0;
  }

  @media (max-width: 1023px) {
    display: none;
  }
}
